@import url(https://fonts.googleapis.com/css?family=Lateef);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.ar {
  font-family: 'Lateef', cursive;
}

.home {
  display: flex;
  align-items: center;
  position: relative;
  text-align: center;
  height: 100vh;
  color: #fff;
  background: rgb(37,37,37);
  background: -webkit-radial-gradient(circle, rgba(37,37,37,1) 0%, rgba(0,0,0,1) 100%);
  background: radial-gradient(circle, rgba(37,37,37,1) 0%, rgba(0,0,0,1) 100%);
}

.home:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0.05;
  height: 100vh;
  width: 100%;
  background-image: url(/static/media/bg.26cc47e4.png);
  background-position: center;
}

.menu {
  position: relative;
  width: 320px;
  margin: 0 auto;
  padding: 50px 0;
  box-shadow: 0 0 20px -5px #000;
  z-index: 10;
}

.menu:before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: -webkit-linear-gradient(transparent, transparent),
  -webkit-linear-gradient(top, rgba(213,173,109,1) 0%, rgba(213,173,109,1) 26%, rgba(226,186,120,1) 35%, rgba(163,126,67,1) 45%, rgba(145,112,59,1) 61%, rgba(213,173,109,1) 100%);
  background: -o-linear-gradient(transparent, transparent);
  z-index: -1;
}

.menu:after {
  content: "";
  position: absolute;
  margin: 8px;
  height: calc(100% - 16px);
  width: calc(100% - 16px);
  top: 0;
  left: 0;
  background: rgb(37,37,37);
  background: -webkit-radial-gradient(circle, rgba(37,37,37,1) 0%, rgba(0,0,0,1) 100%);
  background: radial-gradient(circle, rgba(37,37,37,1) 0%, rgba(0,0,0,1) 100%);
  z-index: -1;
}

.menu .start-title {
  font-size: 15px;
  color: #eee;
}

.menu .start-title .ar {
  font-size: 36px;
  margin-bottom: 10px;
  background: -webkit-linear-gradient(transparent, transparent),
  -webkit-linear-gradient(top, rgba(213,173,109,1) 0%, rgba(213,173,109,1) 26%, rgba(226,186,120,1) 35%, rgba(163,126,67,1) 45%, rgba(145,112,59,1) 61%, rgba(213,173,109,1) 100%);
  background: -o-linear-gradient(transparent, transparent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.menu .title {
  padding: 25px 0;
}

.menu .title h2 {
  font-size: 72px;
  background: -webkit-linear-gradient(transparent, transparent),
  -webkit-linear-gradient(top, rgba(213,173,109,1) 0%, rgba(213,173,109,1) 26%, rgba(226,186,120,1) 35%, rgba(163,126,67,1) 45%, rgba(145,112,59,1) 61%, rgba(213,173,109,1) 100%);
  background: -o-linear-gradient(transparent, transparent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.menu .title h4 {
  font-size: 18px;
  color: #eee;
}

.btn {
  padding: 5px;
  margin: 0 5px;
}

.btn img {
  width: 30px;
}

.surah {
  display: flex;
  flex-wrap: wrap;
}

.surah-list {
  position: fixed;
  width: 240px;
}

.surah-list .list {
  height: 100vh;
  overflow-y: scroll;
}

.surah-list .list::-webkit-scrollbar-track {
}

.surah-list .list::-webkit-scrollbar {
	width: 5px;
}

.surah-list .list::-webkit-scrollbar-thumb {
	background-color: #eee;
}

.surah-list .list .surah-all {
  display: flex;
  background: #000;
  color: #fff;
}

.surah-list .list .surah-active {
  background: #333;
}

.surah-list .list .surah-number {
  display: flex;
  flex-basis: 10px;
  align-items: flex-start;
  padding: 15px;
  margin-top: 15px;
  font-weight: 900;
}

.surah-number p {
  position: relative;
  height: 30px;
  width: 30px;
  color: #000;
  text-align: center;
  line-height: 30px;
  z-index: 1;
}

.surah-number p::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-color: #fff;
  height: 30px;
  width: 30px;
  z-index: -1;
  border-radius: 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.surah-number p::after {
  content: "";
  position: absolute;
  background: #fff;
  top: 25px;
  left: calc(50% - 1px);
  height: 100px;
  width: 2px;
}

.surah-list .list li:last-child p::after {
  display: none;
}

.surah-info li {
  background: #fff;
  color: #000;
  font-size: 11px;
  padding: 2px 5px;
  border-radius: 4px;
  font-weight: 900;
}

.surah-list .list .surah-details {
  padding: 10px 0;
}

.surah-details>p {
  font-size: 22px;
  margin-bottom: 5px;
}

.surah-list .list .ar {
  font-size: 26px;
}

.surah-list .list .surah-all ul>li {
  display: inline-block;
  margin-right: 5px;
}

.surah-content>ul>li.surah-start,
.surah-content>ul>li.surah-head {
  text-align: center;
  padding: 0;
}

.surah-content>ul>li.surah-start {
  font-size: 32px;
  margin-bottom: 15px;
}

.surah-content>ul>li.surah-head {
  width: 220px;
  margin: 0 auto;
  text-align: center;
}

.surah-content>ul>li.surah-head h3 {
  font-size: 32px;
}

.surah-content {
  width: 100%;
  margin-left: 240px;
  padding: 25px;
}

.surah-content>ul>li {
  position: relative;
  padding-right: 50px;
  margin-bottom: 50px;
}

.surah-content>ul>li.all-audio {
  display: inline-block;
  cursor: pointer;
  background: #000;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 15px;
}

.ayah-info {
  position: relative;
}

.ayah-info:after {
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  background: #000;
  height: 2px;
  width: 100%;
  z-index: -1;
}

.ayah-info li {
  display: inline;
  background: #fff;
  font-weight: 900;
  font-size: 11px;
  padding-right: 10px;
  text-transform: uppercase;
}

.ayah-info .sajda {
  background-color: #000;
  color: #fff;
  padding: 1px 3px;
}

.ayah-number {
  position: absolute;
  right: 0;
  top: 18px;
}

.ayah-number p {
  position: relative;
  display: inline-block;
  color: #fff;
  text-align: center;
  height: 30px;
  width: 30px;
  line-height: 30px;
  font-weight: 900;
  z-index: 1;
}

.ayah-number p::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-color: #000;
  height: 30px;
  width: 30px;
  z-index: -1;
  border-radius: 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.ayah-number p::after {
  content: "";
  position: absolute;
  background: #000;
  top: 35px;
  left: calc(50% - 1px);
  height: 340px;
  width: 2px;
  z-index: -1;
}

.surah-content>ul>li:last-child .ayah-number p::after {
  background: #fff;
  height: 200px;
  z-index: -1;
}

.ayah-tr p {
  font-size: 18px;
}

.ayah-tr p:not(.ar):not(.rm) {
  margin: 25px 0;
}

.ayah-tr .ar {
  font-size: 36px;
  background: #eee;
  padding: 5px 15px;
  text-align: right;
}

.ayah-tr .rm {
  text-align: right;
  background: #eee;
  padding: 5px 15px;
  margin-top: 2px;
}

.ayah-audio p {
  display: inline-block;
  cursor: pointer;
  position: absolute;
  top: 18px;
  left: 18px;
  bottom: 60px;
  background: #000;
  color: #fff;
  height: 25px;
  width: 25px;
  text-align: center;
  line-height: 25px;
  z-index: 1;
}

